<template>
  <div class="body">
    <div class="header">
      <Headergoback>
        <span slot="Headertitle"></span>
      </Headergoback>
    </div>

    <div class="container">
      <div class="arrow">
        <span @touchstart="idx=0"
              @touchend="idx=-1"
              @click="scaleD"> </span>
        <span @click="changePdfPage(0)"></span>
        {{currentPage}} / {{pageCount}}
        <span @click="changePdfPage(1)"> </span>
        <span @touchstart="idx=1"
              @touchend="idx=-1"
              @click="scaleX"> </span>
      </div>
      <pdf :src="src"
           ref="wrapper"
           :page="currentPage"
           @num-pages="pageCount=$event"
           @page-loaded="currentPage=$event"
           @loaded="loadPdfHandler">
      </pdf>

    </div>

    <!-- ,,,src -->

    <!-- <pdf :src="src"
         :page="currentPage"
         @num-pages="pageCount=$event"
         @page-loaded="currentPage=$event"
         @loaded="loadPdfHandler">
    </pdf> -->
    <!--
         srcPDF
             page PDF
               num-pages PDF
    page-loaded
    loaded 
         -->
  </div>
</template>
<script>
import pdf from "vue-pdf"
import Headergoback from "components/commen/Header/Headergoback"
export default {
  name: "pdfloader",
  components: {
    pdf,
    Headergoback
  },
  data () {
    return {
      currentPage: 0, // pdf
      pageCount: 0, // pdf
      fileType: 'pdf', // 
      src: '', // pdf
      scale: 100, //
      idx: -1,
    }
  },
  created () {
    this.$loading.show()
    // PDF,
    // console.log(this.$route.query.name);
    this.src = this.$store.state.styfilesdddj + this.$route.query.name
  },
  methods: {
    // PDF,val,0,1
    changePdfPage (val) {
      // console.log(val)
      if (val === 0 && this.currentPage > 1) {
        this.currentPage--
        // console.log(this.currentPage)
      }
      if (val === 1 && this.currentPage < this.pageCount) {
        this.currentPage++
        // console.log(this.currentPage)
      }
    },
    // pdf
    loadPdfHandler (e) {

      this.currentPage = 1 // 
      this.$loading.hide()
    },
    scaleD () {
      this.scale += 5;
      // this.$refs.wrapper.$el.style.transform = "scale(" + this.scale + ")";
      this.$refs.wrapper.$el.style.width = parseInt(this.scale) + "%";
    },
    //
    scaleX () {
      if (this.scale == 100) {
        return;
      }
      this.scale += -5;
      this.$refs.wrapper.$el.style.width = parseInt(this.scale) + "%";
      // this.$refs.wrapper.$el.style.transform = "scale(" + this.scale + ")";
    }
  },
  updated () {
    // this.$loading.hide()
  },
}

</script>
<style scoped>
.body {
  width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.header {
  height: 6vh;
  border-bottom: 1px solid #cfcfcf;
  z-index: 100;
}
.container {
  flex: auto;
  overflow: scroll;
}
.arrow {
  text-align: center;
  position: fixed;
  width: 100%;
  top: 2.4rem;
  z-index: 100;
  background-color: white;
  margin-bottom: 10px;
}
.arrow span {
  padding: 5px;
  display: inline-block;
  padding: 5px;
  color: #cf2d28;
  background: #fac2c0;
  margin: 5px;
  border-radius: 5px;
}
</style>